@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
html{font-size:10px}@media screen and (min-width: 768px){html{font-size:16px}}@media screen and (min-width: 1024px){html{font-size:20px}}body{margin:0;font-family:'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}a{color:#bac1f5;text-decoration:none}::-webkit-scrollbar{width:10px;height:6px}::-webkit-scrollbar-track{border-radius:10px;background:rgba(0,0,0,0.1)}::-webkit-scrollbar-thumb{border-radius:10px;background:rgba(0,0,0,0.2)}::-webkit-scrollbar-thumb:hover{background:rgba(0,0,0,0.7)}::-webkit-scrollbar-thumb:active{background:rgba(0,0,0,0.9)}

.Navbar{position:relative;background:black;display:flex;justify-content:space-between}.Title{color:white;align-self:center;margin-left:0.5rem;font-weight:bold}.Link{display:inline-block;color:white;padding:0.5rem;transition:all 0.25s ease}.Link:hover{color:black;background:white}.Menu .active{color:black;background:white}

.Contact{position:fixed;left:50%;margin-left:-10rem;display:flex;flex-direction:column;background:rgba(0,0,0,0.5);outline:0.5px solid white;transition:bottom 0.25s ease;font-size:2rem}.Contact a{margin-left:1rem;margin-right:1rem;text-align:center;color:white}.Contact a:last-child{margin-bottom:0}.List{height:2.5rem;display:flex}.Arrow{color:white;font-size:1.5rem;background:transparent;border:none;cursor:pointer}.Arrow:focus{outline:none}

.Introduction{height:90vh;padding:1.5rem;background-image:url(/SynQ-Website/static/media/AboutClubPhone.77e7d490.webp);background-size:cover;background-position:center;background-color:rgba(0,0,0,0.5);background-blend-mode:hue;-webkit-clip-path:polygon(0% 0%, 0% 90%, 50% 100%, 100% 90%, 100% 0%);clip-path:polygon(0% 0%, 0% 90%, 50% 100%, 100% 90%, 100% 0%)}@media screen and (min-width: 768px){.Introduction{background-image:url(/SynQ-Website/static/media/AboutClub.7cb3050e.webp)}}.IntroContent{width:50%;color:white;text-align:justify}

.CourseInfo{padding:1.5rem;display:flex;justify-content:space-between}.CourseContent{width:50%}.CoursePic{align-self:center;width:45%;height:60vh;border-radius:10px;background-image:url(/SynQ-Website/static/media/CoursePhone.3f695293.webp);background-size:cover;background-position:60% center}@media screen and (min-width: 768px){.CoursePic{background-image:url(/SynQ-Website/static/media/Course.09e8281e.webp);height:90vh;background-position:center 10%}}

.Members{padding:1.5rem;background:#ffe799}.MemberCardsContainer{display:flex;justify-content:space-between;flex-wrap:wrap}

.MemberCard{position:relative;width:45vw;height:45vw;overflow:hidden;cursor:pointer;margin-bottom:1rem;font-size:1.2rem}@media screen and (min-width: 768px){.MemberCard{width:20vw;height:20vw}}.MemberCard:focus{outline:none}.MemberCard img{width:100%;height:100%}.MemberCard div{position:absolute;left:0;width:100%;height:100%;display:flex;flex-direction:column;justify-content:space-around;align-items:center;background:rgba(0,0,0,0.25);color:white;transition:all 0.5s ease;transform:translateZ(0)}.MemberCard div h5{margin:0}


.SignUpIntro{height:90vh;padding:1.5rem;background-image:url(/SynQ-Website/static/media/SignUpBgPhone.8b11f6c2.jpg);background-color:rgba(0,0,0,0.4);background-blend-mode:hue;background-size:cover;background-position:center;-webkit-clip-path:polygon(0% 0%, 0% 90%, 50% 100%, 100% 90%, 100% 0%);clip-path:polygon(0% 0%, 0% 90%, 50% 100%, 100% 90%, 100% 0%);display:flex;justify-content:center;align-items:center}@media screen and (min-width: 768px){.SignUpIntro{background-image:url(/SynQ-Website/static/media/SignUpBg.2ab8272d.jpg)}}.SignUpIntro h2{color:white}

.SignUpForm{display:flex;justify-content:center;padding:2rem;color:white}.InputContainer{padding:2rem;width:80%;border-radius:2rem;background:black;display:flex;flex-direction:column}@media screen and (min-width: 768px){.InputContainer{width:50%}}.InputContainer button{align-self:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0.5rem 2rem 0.5rem 2rem;border-radius:2rem;margin-bottom:2rem;font-size:1rem;color:white;background:#2195EE;border:none;cursor:pointer}.InputContainer button:focus{outline:none}.InputContainer .msg{align-self:center}

.TextInput{display:flex;flex-direction:column;margin-bottom:2rem}.TextInput .field{border:none;border-radius:1rem;height:2rem;font-size:1rem;padding:0.5rem 1rem;background:#E8F0FE}.TextInput .field:focus{outline:none}.TextInput .error{font-size:0.8rem;color:#FF2400}

.TextAreaInput{display:flex;flex-direction:column;margin-bottom:2rem}.TextAreaInput .field{resize:none;height:5rem;border:none;border-radius:1rem;font-size:1rem;padding:1rem;background:#E8F0FE}.TextAreaInput .field:focus{outline:none}

.CheckBox{margin-bottom:2rem;color:white;font-size:0.8rem}.error{font-size:0.8rem;color:#FF2400}input[type=checkbox]{width:0.8rem;height:0.8rem;margin:0 0.2rem 0 0}

.FAQ{display:flex;flex-direction:column;align-items:center}

.QACard{width:80vw;margin:1rem;color:white}@media screen and (min-width: 768px){.QACard{width:60vw}}.QACard .Question{cursor:pointer;padding:1rem;background-color:black}.QACard .Answer{overflow:hidden;transition:opacity 1s ease, max-height 0.75s ease;background:grey;transform:translateZ(0);--webkit-transform: translateZ(0)}

